import React from "react";
import Navbar from "../Navbar/Navbar";
import styles from "./Layout.module.scss";
import classNames from 'classnames'
const Layout = ({ nickName, avatarUrl, id, children, renderNavBar = true }) => {
  return (
    <div className={styles.layout}>
      {renderNavBar && (
        <div>
          <Navbar nickName={nickName} avatarUrl={avatarUrl} id={id} />
        </div>
      )}
      <div className={classNames(styles.container, renderNavBar && styles.container_paddingTop)}>{children}</div>
    </div>
  );
};

export default Layout;
