import React, { useState } from "react";
import Comment from "../Comment/Comment";
import UserBadge from "../UserBadge/UserBadge";
import styles from "./DetailedCard.module.scss";
const DetailedCard = ({
  userName,
  avatarUrl,
  userId,
  imgUrl,
  likes,
  isLikedByYou,
  comments,
}) => {
  const [isCommentsShown,setIsCommentsShown] = useState(false)

    const renderComments = () =>{
      if (comments.length > 2 && !isCommentsShown) {
        const commentsForRender = [...comments]
        commentsForRender.splice(comments.length - 2,2)
        return (
          <>
            <span className={styles.detailedCard__comments_btn_all} onClick={() => setIsCommentsShown(true)}>показати ще комментарі</span>
            {commentsForRender.map((comment) => <Comment key={comment.text} {...comment} />)}
          </>
        )
      }
      return (
        <>
          {comments.map((comment) => <Comment {...comment} />)}
        </>
      )
    }
  return (
    <div className={styles.detailedCard}>
      <div className={styles.detailedCard__header}>
        <UserBadge nickName={userName} avatarUrl={avatarUrl} id={userId} />
      </div>
      <div >
        <img className={styles.detailedCard__img} src={imgUrl} alt="img" />
      </div>
      <div className={styles.detailedCard__btn}>
        <i  className={`fa-${isLikedByYou ? "solid" : "regular"} ${styles.detailedCard__icons} fa-heart`}></i>
        <i className={`${styles.detailedCard__icons} fa-regular fa-comment`}></i>
      </div>
      <div className={styles.detailedCard__likes}>оцінили {likes} людей</div>
      <div className={styles.detailedCard__comments}>
        {renderComments()}
      </div>
      <textarea className={styles.detailedCard__textarea}></textarea>
    </div>
  );
};

export default DetailedCard;
