import React from "react";
import { useState } from "react";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import DetailedCard from "../../components/DetailedCard/DetailedCard";
import Layout from "../../components/Layout/Layout";
import Loader from "../../components/Loader/Loader";
import { usePostsData } from "../../hooks/use-posts";
import { changeFetching, getPostsFirebase } from "../../store/slice/postsSlice";
import styles from "./MainPage.module.scss";
const MainPage = () => {
  const postsData = usePostsData();
  const dispatch = useDispatch();

  useEffect(() => {
    if (
      (postsData.fetching &&
        postsData.posts.length < postsData.totalCountPosts) ||
      !postsData.totalCountPosts
    ) {
      dispatch(
        getPostsFirebase({
          limitPosts: 2,
        })
      );
    }
  }, [postsData.fetching]);

  useEffect(() => {
    const scrollHangler = (e) => {
      if (
        e.target.documentElement.scrollHeight -
          (e.target.documentElement.scrollTop + window.innerHeight) <
        100
      ) {
        dispatch(changeFetching(true));
      }
    };
    document.addEventListener("scroll", scrollHangler);
    return () => {
      document.removeEventListener("scroll", scrollHangler);
    };
  }, []);
  return (
    <Layout
      nickName={"meridaura"}
      avatarUrl={
        "https://ss-ua.akinoncdn.com/products/2021/11/08/370532/978cfbea-a74e-44b8-a77e-24524e9d90a1.jpg"
      }
      id={"5"}
    >
      {postsData.posts.map((e) => (
        <DetailedCard
          key={e.id}
          userName="ludpcka"
          isLikedByYou={true}
          likes="10"
          imgUrl={e.img}
          comments={[
            {
              text: "qxeqwxeqweqwe",
              nickName: "denus lox",
            },
            {
              text: "123",
              nickName: "denus lox154",
            },
            {
              text: "123",
              nickName: "denus lox154",
            },
          ]}
          avatarUrl="data:image/jpeg;base64,"
        />
      ))}
      {postsData.status === "loading" && <Loader />}
      {postsData.status === "error" ? (
        <div className={styles.error_text}>
          при завантажені данних сталась помилка
        </div>
      ) : (
        postsData.status !== "loading" &&
        postsData.posts.length === postsData.totalCountPosts && (
          <div className={styles.ended_posts_text}>ви подивилися всі пости</div>
        )
      )}
    </Layout>
  );
};

export default MainPage;
