import React from "react";
import {Link} from 'react-router-dom'
import styles from "./UserBadge.module.scss";
const UserBadge = ({ nickName, avatarUrl, id }) => {
  return (
    <Link className={styles.userBadgeLink} to={id ? `/user/${id}` : '/'}>
      <div className={styles.userBadge}>
        {avatarUrl ? (
          <img
            src={avatarUrl}
            className={styles.userBadge__img}
            alt="user-img"
          />
        ) : (
          <div className={styles.notImg}></div>
        )}
        <span className={styles.userBadge__name}>{nickName}</span>
      </div>
    </Link>
  );
};

export default UserBadge;
