import React, { useState } from "react";
import Form from "../../components/Form/Form";
import Logo from "../../ui/Logo/Logo";
import styles from "./RegistrationPage.module.scss";
import { Link } from "react-router-dom";
import Avatar from "react-avatar-edit";
import AvatarEditImg from "../../ui/img/AvatarEditImg";
import { getAuth, createUserWithEmailAndPassword } from "firebase/auth";
const RegistrationPage = () => {
  const [showAvatarEditor, setShowAvatarEditor] = useState(false);
  const [formData, setFormData] = useState({
    email: "",
    password: "",
    name: "",
    img: "",
  });
  const registrations = (e) => {
    e.preventDefault();
    const auth = getAuth();
    createUserWithEmailAndPassword(auth,formData.email, formData.password)
      .then((userCredential) => {
        const user = userCredential.user;
        console.log(user);
      })
      .catch((error) => {
        const errorCode = error.code;
        const errorMessage = error.message;
      });
  };
  const toggleAvatarEditor = () => {
    setShowAvatarEditor(prev => !prev)
  };



  return (
    <div className={styles.registrations}>
      <div className={styles.logo}>
        <Logo />
      </div>
      <Form onSubmit={registrations}>
        <div onClick={toggleAvatarEditor} className={styles.avatar}>
          <div className={styles.avatar__edit}>
            <div className={styles.avatar__edit_img}>
              <AvatarEditImg />
            </div>
          </div>
          <img
             src={formData.img ? formData.img : "https://www.svgrepo.com/show/49569/avatar.svg"}
            className={styles.avatar__img}
            alt="img"
          />
        </div>
        {showAvatarEditor ? (
          <div className={styles.avatar__editor}>
            <Avatar onClose={toggleAvatarEditor} onCrop={(src) => setFormData({
              ...formData,
              img: src,
            })}  imageWidth={320} width={300} />
          </div>
        ) : (
          <>
            <input
              onChange={(e) =>
                setFormData({
                  ...formData,
                  name: e.target.value,
                })
              }
              minLength={4}
              type="text"
              placeholder="введіть ваше ім`я"
            />
            <input
              onChange={(e) =>
                setFormData({
                  ...formData,
                  email: e.target.value,
                })
              }
              type="email"
              placeholder="введіть ваш емаіл"
            />
            <input
              onChange={(e) =>
                setFormData({
                  ...formData,
                  password: e.target.value,
                })
              }
              minLength={5}
              type="password"
              placeholder="введіть ваш пароль"
            />
            <button type="submit">зарегеструватися</button>
          </>
        )}
        <div className={styles.and}>або</div>
        <div>
          <Link to={"/login"}>ввійти в акаунт</Link>
        </div>
      </Form>
    </div>
  );
};

export default RegistrationPage;
