import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import {
  query,
  getCountFromServer,
  limit,
  startAfter,
} from "firebase/firestore";
import { getDocs, collection } from "firebase/firestore";
import { db } from "../../firebase/index";

let lastPost = null;

export const getPostsFirebase = createAsyncThunk(
  "postsSlice/getPostsFirebase",
  async function ({ limitPosts }, { rejectWithValue }) {
    try {
      const refCollectionsPosts = collection(db, "posts");
      const refPosts = query(refCollectionsPosts, limit(limitPosts));
      if (lastPost) {
        const nextPostsRef = query(
          refCollectionsPosts,
          startAfter(lastPost),
          limit(limitPosts)
        );
        const nextPostsData = await getDocs(nextPostsRef);
        lastPost = nextPostsData.docs[nextPostsData.docs.length - 1];
        const totalCountPosts = (
          await getCountFromServer(refCollectionsPosts)
        ).data().count;
        return {
          posts: nextPostsData.docs.map((e) => e.data()),
          totalCountPosts: totalCountPosts,
        };
      }

      const totalCountPosts = (
        await getCountFromServer(refCollectionsPosts)
      ).data().count;

      const postsData = await getDocs(refPosts);

      lastPost = postsData.docs[postsData.docs.length - 1];

      return {
        posts: postsData.docs.map((e) => e.data()),
        totalCountPosts: totalCountPosts,
      };
    } catch (error) {
      return rejectWithValue(error.message);
    }
  }
);
const initialState = {
  posts: [],
  status: null,
  totalCountPosts: 0,
  fetching: true,
  error: null,
};

export const postsSlice = createSlice({
  name: "post",
  initialState,
  reducers: {
    changeFetching: (state, actions) => {
      state.fetching = actions.payload;
    },
  },
  extraReducers: {
    [getPostsFirebase.pending]: (state) => {
      state.status = "loading";
    },
    [getPostsFirebase.fulfilled]: (state, actions) => {
      state.status = "resolved";
      state.error = null;
      state.posts = [...state.posts, ...actions.payload.posts];
      state.totalCountPosts = actions.payload.totalCountPosts;
      state.fetching = false;
    },
    [getPostsFirebase.rejected]: (state, actions) => {
      state.status = "error";
      state.error = actions.payload;
    },
  },
});

export const { changeFetching } = postsSlice.actions;

export default postsSlice.reducer;
